interface QuestionSelectorProps {
  category: CounselingCategory;
  language: Language;
  onQuestionSelect: (question: string) => void;
}

export const QuestionSelector: React.FC<QuestionSelectorProps> = ({
  category,
  language,
  onQuestionSelect
}) => {
  const [selectedSubCategory, setSelectedSubCategory] = useState<string>('');
  const subCategories = categoryQuestions[category] || [];

  return (
    <div style={{
      marginTop: '15px',
      direction: language === 'fa' ? 'rtl' : 'ltr'
    }}>
      {/* Subcategories */}
      <div style={{
        display: 'flex',
        gap: '10px',
        marginBottom: '15px',
        flexWrap: 'wrap'
      }}>
        {subCategories.map((subCat) => (
          <button
            key={subCat.id}
            onClick={() => setSelectedSubCategory(
              selectedSubCategory === subCat.id ? '' : subCat.id
            )}
            style={{
              padding: '8px 16px',
              borderRadius: '20px',
              border: 'none',
              background: selectedSubCategory === subCat.id ? '#2196f3' : '#e3f2fd',
              color: selectedSubCategory === subCat.id ? 'white' : '#2196f3',
              cursor: 'pointer',
              fontSize: '14px',
              transition: 'all 0.2s',
              '&:hover': {
                background: selectedSubCategory === subCat.id ? '#1976d2' : '#bbdefb'
              }
            }}
          >
            {subCat.title[language]}
          </button>
        ))}
      </div>

      {/* Questions for selected subcategory */}
      {selectedSubCategory && (
        <div style={{
          display: 'flex',
          flexDirection: 'column',
          gap: '10px'
        }}>
          {subCategories
            .find(sc => sc.id === selectedSubCategory)
            ?.questions.map((q, index) => (
              <button
                key={index}
                onClick={() => onQuestionSelect(q[language])}
                style={{
                  padding: '12px',
                  borderRadius: '8px',
                  border: '1px solid #e0e0e0',
                  background: 'white',
                  color: '#333',
                  cursor: 'pointer',
                  fontSize: '14px',
                  textAlign: 'left',
                  transition: 'all 0.2s',
                  '&:hover': {
                    background: '#f5f5f5',
                    borderColor: '#2196f3'
                  }
                }}
              >
                {q[language]}
              </button>
            ))}
        </div>
      )}
    </div>
  );
};

export default QuestionSelector;
